import React from 'react'
import data from '../data.js';
import Card from './Card.js';

const Short = () => {
    const items = data.filter((item) => {
        return item.list == "short"
    })
    return (
        <>
            <div className="wrap">
                {
                    items.map((item) => {
                        return <Card key={item.id} item={item} />
                    })
                }
            </div>
        </>
    )
}

export default Short
