import 'bootstrap/dist/css/bootstrap.min.css';
import { Container, Nav, Navbar, Row, Col, Form, Button } from 'react-bootstrap'
import './App.css';
import { Routes, Route, Link, Outlet } from "react-router-dom"
import Home from './page/Home';
import Shop from './page/Shop';
import Lookbook from './page/Lookbook';
import Event from './page/Event';
import Long from './page/Long';
import Short from './page/Short';
import Footer from './Footer';
import data from './data.js';
import Detail from './page/Detail.js';
import { useState } from 'react';



function App() {
  const [query, setQuery] = useState('');
  const [search, setSearch] = useState('')

  const searchChange = (e) => {
    console.log("1")
    setQuery(e.target.value);

  }
  const searchKey = (e) => {
    console.log("2")
    setSearch(query);


  }
  const searchButton = (e) => {
    e.preventDefault();
    setSearch(query);
  }
  const remove = (str) => {
    return str.split(' ').join('')
  }

  const filterData = data.filter((item) => {
    return (
      remove(item.content).includes(remove(search))
    )
  })

  return (
    <>
      <Navbar bg="light" data-bs-theme="light">
        <Container>
          <Navbar.Brand as={Link} to="/">ZARA</Navbar.Brand>
          <Nav className="me-auto">
            <Nav.Link as={Link} to="/lookbook">LOOKBOOK</Nav.Link>
            <Nav.Link as={Link} to="/shop">SHOP</Nav.Link>
          </Nav>

          <Form inline onSubmit={searchButton}>
            <Row>
              <Col xs="auto">
                <Form.Control
                  type="text"
                  placeholder=" "
                  className=" mr-sm-2"
                  onChange={searchChange}
                  onkeyDown={searchKey}
                />
              </Col>
              <Col xs="auto">
                <Button type="submit" onClick={searchButton}></Button>
              </Col>
            </Row>
          </Form>
        </Container>
      </Navbar>

      <Routes>
        <Route path="/" element={<Home filterData={filterData} />}></Route>
        <Route path="/shop" element={<Shop />}>
          <Route path="long" element={<Long />} />
          <Route path="short" element={<Short />} />
        </Route>
        <Route path="/lookbook" element={<Lookbook />}></Route>
        <Route path="/product/:id" element={<Detail data={data} />}></Route>
      </Routes>

      <Footer />
    </>
  );
}

export default App;
