import React from 'react'
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';
import 'swiper/css/pagination';
import 'swiper/css/navigation';
import { Autoplay, Pagination, Navigation } from 'swiper/modules';
import Card from './Card.js';
import { useState } from 'react';


const home = ({ filterData }) => {

    const slide1 = {
        backgroundImage: `url(${process.env.PUBLIC_URL}/img/vz2.jpg)`,
    };
    const slide2 = {
        backgroundImage: `url(${process.env.PUBLIC_URL}/img/vz4.jpg)`,
    }
    const slide3 = {
        backgroundImage: `url(${process.env.PUBLIC_URL}/img/vz5.jpg)`,
    }
    const slide4 = {
        backgroundImage: `url(${process.env.PUBLIC_URL}/img/vz6.jpg)`,
    }


    return (
        <div>
            <Swiper
                spaceBetween={30}
                centeredSlides={true}
                autoplay={{
                    delay: 2500,
                    disableOnInteraction: false,
                }}
                pagination={{
                    clickable: true,
                }}
                navigation={true}
                modules={[Autoplay, Pagination, Navigation]}
                className="mySwiper"
            >
                <SwiperSlide style={slide1}><h2>ZARA LIFE STYLE</h2></SwiperSlide>
                <SwiperSlide style={slide2}>
                    <h3>ZARA COLLECTION03</h3>
                    <div>more</div>
                    <p>By Vincent Van Duysen
                        <br />A Modern Sanctuary</p>
                </SwiperSlide>
                <SwiperSlide style={slide3}>
                    <h3>ZARA HOME</h3>
                    <div>
                        
                    </div>
                </SwiperSlide>
                <SwiperSlide style={slide4}>
                    <h2>ZARA <br />STYLE</h2>
                </SwiperSlide>
            </Swiper>
            <div className="wrap">
                {
                    filterData.map((item) => {
                        return <Card key={item.id} item={item} />
                    })
                }
            </div>
        </div>
    )
}

export default home
