import React from 'react'
import 'bootstrap/dist/css/bootstrap.min.css';
import { Tab, Tabs } from 'react-bootstrap'
import { Link, Outlet } from "react-router-dom"

import Long from './Long';
import Short from './Short';

const Shop = () => {
    const benner = {
        backgroundImage: `url(${process.env.PUBLIC_URL}/img/shop_vi_02.jpg)`,
    }
    return (
        <>
            <div className="shop_bennerV" style={benner}></div>


            <Tabs
                defaultActiveKey="home"
                id="uncontrolled-tab-example"
                className="mb-3"
            >
                <Tab eventKey="home" title="long">
                    <Link to="long" />
                    <Long />
                </Tab>
                <Tab eventKey="profile" title="short">
                    <Link to="short" />
                    <Short />
                </Tab>
            </Tabs>

            <Outlet></Outlet>

        </>
    )
}

export default Shop
